.header {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: 1rem;
  background-color: rgba(204, 204, 204, 0.719);
  width: 100%;
  height: 100%;
}

.header h1 {
  width: 60%;
}

.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.App-card {
  width: 35%;
  height: 22vh;
  display: flex;
  margin: 0.8rem;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background-color: #ccc;
  padding: 1rem;
  transition: all 0.5s ease-in-out;
}

.App-card:hover {
  transform: scale(1.05);
}
